import React from "react";
import { connect } from "react-redux";
import {
  Auth,
  Basic,
  DistributedVacantPostsReport__Interface,
  FC_DistributedVacantPosts,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import ExportToExcel from "../../components/GenerateReport/ExportToExcel";
import Heading from "../../components/Heading/Heading";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";
// import Alert, { AlertInterface } from "../../components/Alert/Alert";

// props for the component
interface AppProps {
  auth: Auth;
  basic: Basic;
}

interface AppState {
  loading: boolean;
  data: DistributedVacantPostsReport__Interface[];
  error: {
    target: "main-error" | "main-success" | null;
    msg: string;
  };
}

export interface DistributionVacant_Qualification {
  qualification_id: string;
  qualification_name: string;
  total: number;
  limits: number;
  distributed: number;
  remaining: number;
}
export interface API_DistributedVacantPosts {
  district_code: string;
  district_name: string;
  teachers_on_qualification: DistributionVacant_Qualification[];
}

class _DistributedVaccantReport extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: true,
      data: [],
      error: {
        target: null,
        msg: "",
      },
    };
  }

  componentDidMount() {
    if (!this.props.basic.academic_calender)
      return this.setState({
        error: {
          target: "main-error",
          msg: "No academic_calender found!, please try again later",
        },
      });
    this.setState({
      loading: true,
    });
    FC_DistributedVacantPosts("1", (status, data, msg) => {
      this.setState({
        loading: false,
        data: data,
        error: status
          ? { msg: "", target: null }
          : { target: "main-error", msg },
      });
    });
  }

  render() {
    if (this.state.loading === true)
      return (
        <div className="py-8 text-center">
          <Loading className="mx-auto" />
        </div>
      );
    return (
      <div className="max-w-screen-lg  mx-auto bg-white p-2 rounded ">
        {this.state.error.target === "main-error" && (
          <Alert theme="danger">{this.state.error.msg}</Alert>
        )}
        {this.state.error.target === "main-success" && (
          <Alert theme="success">{this.state.error.msg}</Alert>
        )}
        <div className="p-1 py-2 flex">
          <section className="flex-1">
            <Heading>Distributed Vacant Posts</Heading>
            <h4 className="text-gray-700">
              {this.props.basic.academic_calender?.academic_year_name}
            </h4>
          </section>
          <section>
            <ExportToExcel
              fileData={this.state.data}
              fileName={`REPORT OF DISTRIBUTED VACANT POSTS ${this.props.basic.academic_calender?.academic_year_name}`}
              btnName="Download EXCEL"
            />
          </section>
        </div>

        <div className="overflow-x-auto">
          <table className="">
            <thead>
              <tr>
                <th className="p-2 border" rowSpan={2}>
                  #
                </th>
                <th className="p-2 border" rowSpan={2}>
                  District code
                </th>
                <th className="p-2 border" rowSpan={2}>
                  District name
                </th>

                <th className="p-2 border bg-blue-100" colSpan={4}>
                  A0
                </th>

                <th className="p-2 border bg-yellow-100" colSpan={4}>
                  A1
                </th>

                <th className="p-2 border bg-indigo-100" colSpan={4}>
                  A2
                </th>

                <th className="p-2 border bg-green-100" colSpan={4}>
                  District
                </th>
              </tr>

              <tr>
                {/* <th className="p-2 border" >
                  #
                </th> */}
                {/* <th className="p-2 border">district_code</th>
                <th className="p-2 border">district_name</th> */}

                <th className="p-2 border bg-opacity-50 bg-blue-100">
                  distributed
                </th>
                <th className="p-2 border bg-opacity-50 bg-blue-100">limits</th>
                <th className="p-2 border bg-opacity-50 bg-blue-100">
                  remaining
                </th>
                <th className="p-2 border bg-opacity-50 bg-blue-100">total</th>

                <th className="p-2 border bg-opacity-50 bg-yellow-100">
                  distributed
                </th>
                <th className="p-2 border bg-opacity-50 bg-yellow-100">
                  limits
                </th>
                <th className="p-2 border bg-opacity-50 bg-yellow-100">
                  remaining
                </th>
                <th className="p-2 border bg-opacity-50 bg-yellow-100">
                  total
                </th>

                <th className="p-2 border bg-opacity-50 bg-indigo-100">
                  distributed
                </th>
                <th className="p-2 border bg-opacity-50 bg-indigo-100">
                  limits
                </th>
                <th className="p-2 border bg-opacity-50 bg-indigo-100">
                  remaining
                </th>
                <th className="p-2 border bg-opacity-50 bg-indigo-100">
                  total
                </th>

                <th className="p-2 border bg-opacity-50 bg-green-100">
                  District distributed
                </th>
                <th className="p-2 border bg-opacity-50 bg-green-100">
                  District limits
                </th>
                <th className="p-2 border bg-opacity-50 bg-green-100">
                  District remaining
                </th>
                <th className="p-2 border bg-opacity-50 bg-green-100">
                  District total
                </th>
              </tr>
            </thead>
            <tbody>
              {this.state.data.length <= 0 ? (
                <tr>
                  <td colSpan={40}>No data found</td>
                </tr>
              ) : (
                this.state.data.map((el, i) => (
                  <tr key={i}>
                    <td className="p-2 border">{i + 1}</td>
                    <td className="p-2 border">{el.district_code}</td>
                    <td className="p-2 border">{el.district_name}</td>

                    <td className="p-2 border bg-opacity-40 bg-blue-100">
                      {el.A0_distributed}
                    </td>
                    <td className="p-2 border bg-opacity-40 bg-blue-100">
                      {el.A0_limits}
                    </td>
                    <td className="p-2 border bg-opacity-40 bg-blue-100">
                      {el.A0_remaining}
                    </td>
                    <td className="p-2 border bg-opacity-40 bg-blue-100">
                      {el.A0_total}
                    </td>

                    <td className="p-2 border bg-opacity-40 bg-yellow-100">
                      {el.A1_distributed}
                    </td>
                    <td className="p-2 border bg-opacity-40 bg-yellow-100">
                      {el.A1_limits}
                    </td>
                    <td className="p-2 border bg-opacity-40 bg-yellow-100">
                      {el.A1_remaining}
                    </td>
                    <td className="p-2 border bg-opacity-40 bg-yellow-100">
                      {el.A1_total}
                    </td>

                    <td className="p-2 border bg-opacity-40 bg-indigo-100">
                      {el.A2_distributed}
                    </td>
                    <td className="p-2 border bg-opacity-40 bg-indigo-100">
                      {el.A2_limits}
                    </td>
                    <td className="p-2 border bg-opacity-40 bg-indigo-100">
                      {el.A2_remaining}
                    </td>
                    <td className="p-2 border bg-opacity-40 bg-indigo-100">
                      {el.A2_total}
                    </td>

                    <td className="p-2 border bg-opacity-40 bg-green-100">
                      {el.district_total_distributed}
                    </td>
                    <td className="p-2 border bg-opacity-40 bg-green-100">
                      {el.district_total_limits}
                    </td>
                    <td className="p-2 border bg-opacity-40 bg-green-100">
                      {el.district_total_remaining}
                    </td>
                    <td className="p-2 border bg-opacity-40 bg-green-100">
                      {el.district_total}
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const DistributedVaccantReport = connect(
  mapStateToProps,
  {}
)(_DistributedVaccantReport);
